* {
  box-sizing: border-box;
}

body {
  color: rgb(45, 46, 48);
  padding: 2rem 5rem;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
}

p {
  color: rgba(45, 46, 48, 0.75);
  line-height: 1.3;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  margin-bottom: 20px;
}

.container {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 34px;
  max-width: 1440px;
}

.image-of-content {
  flex: 0 1 auto;
}

.content {
  flex: 1 1 10rem;
}

.container imgage-of-content {
  object-fit: cover;
  max-width: 100%;
}

.btn {
  background: #c8a597;
  color: #eeecec;
  border-style: none;
  border-radius: 32px;
  width: 128px;
  margin: 20px auto;
  padding: 16px 40px;
  cursor: pointer;
}

.modal {
  position: fixed;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: visibility 0s, opacity 0.4s;
}
.modal--hidden {
  display: none;
}
.modal__box {
  background-color: white;
  padding: 20px 3rem;
  margin: auto;
  width: 80%;
  position: fixed;
}
.modal__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modal__close {
  cursor: pointer;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.overlay--hidden {
  display: none;
}

@media screen and (min-width: 905px) {
  .container img {
    float: left;
    width: 100%;
  }
}
@media screen and (max-width: 860px) {
  body {
    font-size: smaller;
  }
  .btn {
    align-items: center;
  }
  .container {
    display: inline-block;
  }
  .container img {
    float: left;
    width: 100%;
  }
  .modal__box {
    bottom: 12rem;
    left: 10%;
    top: 30%;
  }
  .modal__box__btn-reset {
    left: 30px;
  }
}